
code {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  word-wrap: normal;
  background-color: rgba(27,31,35,.05);
  border-radius: 3px;
  font-size: 85%;
  margin: 0;
  padding: .2em .4em;
}

pre code {
  padding: 16px;
  font-size: 95%;
  line-height: 1.5;
  display: block;
  text-shadow: 0 1px #fff;
}
pre {
  margin-bottom: 18px;
  max-height: 35em;
  font-size: 85%;
  line-height: 1.45;
  position: relative;
  overflow: auto;
  background-color: #f6f8fa;
  border-radius: 3px;
}
del {
  color: #888;
}
pre code {
  background: none;
  overflow-wrap: normal;
  white-space: inherit;
}
ul, li, ol, dl, dt {
  list-style: inherit;
}
ul, ol {
  padding-left: 2em;
}
dl {
  padding: 0;
  dt {
    padding: 0;
    margin-top: 16px;
    font-size: 14px;
    font-style: italic;
    font-weight: 600;
  }
}
li + li {
  margin-top: 3px;
}
a {
  color: #0366d6;
  text-decoration: none;
}
p, ol, ul {
  margin-bottom: 16px;
}
li {
  display: list-item;
}

h1 {
  padding-bottom: 0.3em;
  font-size: 2em;
  border-bottom: 1px solid #cecece;
  margin-bottom: 10px;
}

h2 {
  margin-bottom: 10px;
  padding-bottom: 0.3em;
  font-size: 1.5em;
  border-bottom: 1px solid #cecece;
}

h3 {
  margin-bottom: 10px;
  font-size: 1.25em
}

h4 {
  margin-bottom: 8px;
  font-size: 1em
}

h5 {
  margin-bottom: 8px;
  font-size: 0.875em
}

h6 {
  margin-bottom: 5px;
  font-size: 0.85em;
  color: #6a737d
}

blockquote {
  margin: 0;
  padding: 0 1em;
  margin-bottom: 10px;
  color: #6a737d;
  border-left: 0.25em solid #dfe2e5;
  & >:first-child {
    margin-top: 0;
  }
  & >:last-child {
    margin-bottom: 0;
  }
}

hr {
  height: 0.25em;
  padding: 0;
  margin: 24px 0;
  background-color: #e1e4e8;
  border: 0;
}


table {
  margin-bottom: 16px;
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  display: block;
  width: 100%;
}

table th {
  font-weight: 600;
}

table th,table td {
  padding: 6px 13px;
  border: 1px solid #dfe2e5;
}

table tr {
  background-color: #fff;
  border-top: 1px solid #c6cbd1;
}

table tr:nth-child(2n) {
  background-color: #f6f8fa;
}

table img {
  background-color: transparent;
}

img {
  max-width: 100%;
  box-sizing: content-box;
  vertical-align: middle;
}

img[align=right] {
  padding-left: 20px;
}

img[align=left] {
  padding-right: 20px;
}